.cover-container {
  height: 100vh;
  width: 100%;
  background-color: black;
  object-fit: contain;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.6);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.video {
  object-fit: cover;
  width: 100%;
  height: 100vh;
  position: absolute;
  z-index: 0;
  -o-filter: blur(5px);
  filter: blur(5px);
}

.cover-container > h1 {
  color: white;
  font-size: 100px;
}

.cover-container > p {
  margin-top: 8px;
  color: white;
  font-size: 32px;
}

@media screen and (max-width: 960px) {
  .cover-container > h1 {
    font-size: 70px;
    text-align: center;
  }
  .cover-container > p {
    text-align: center;
    font-size: 22px;
    width: 85%;
  }
}
.carousel-container {
  display: flex;
  vertical-align: center;
  align-items: center;
  background-color: black;
  padding: 30px 0 30px 0;
}

.carousel-title {
  color: white;
  width: 40%;
  text-align: center;
  font-size: 30px;
  padding: 0 100px 0 100px;
}

.slide-container {
  height: 80%;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  cursor: pointer;
}
.slide-container img {
  object-fit: contain;
  object-position: center center;
  transition: all 0.2s ease-in-out;
}
.slide-container img:hover {
  transform: scale(1.2);
  opacity: 0.7;
}

.slide-desc {
  background-color: black;
  position: absolute;
  bottom: 0;
  right: 0;
  width: 70%;
  padding-left: 30px;
  border-top-left-radius: 20px;
  opacity: 0.8;
}
.slide-desc span {
  color: white;
  display: block;
  font-size: 22px;
  padding: 20px 0 20px 10px;
}

@media only screen and (max-width: 960px) {
  .carousel-container {
    flex-direction: column;
  }
  .carousel-title {
    width: 100%;
    text-align: center;
    padding: 50px 0 50px 0;
  }
  .slide-container {
    width: 100%;
  }
}